                  <div class="nav-center">
                    <a v-on:click="language = 'english'" v-bind:class="{ 'nav-item':true , 'is-active': language == 'english' }" >English</a>
                    <a v-on:click="language = 'spanish'" v-bind:class="{ 'nav-item':true , 'is-active': language == 'spanish' }" >Español</a>
