                    <p>Thanks for visiting my page!</p>
                  </div>
                  <div v-show="language == 'spanish'">
                    <p>Hola, mi nombre es<br>
                      <span class="is-oswald">Guillermo CROPPI</span><br></p>
                    <p>¡Gracias por visitar mi página!</p>
                  </div>
                </div>
                <img src="../assets/header-pic.jpg" class="image" alt="">
                <div class="with-padding content">
                  <div v-show="language == 'english'">
                    <p>I am a system engineer, and I am a fullstack web developer</p>
                    <p>I work as a freelancer, cause I like to meet my clients, and work with them</p>
                    <p>I don't make system.
                      <b>I make solutions</b>. Solutions that responds your needs and solves your problems. Yes, it involves systems, sure but, what isn't nowadays, right?</p>
                    <p>¿Why choose me? Cause I take care of my systems, I deliver presentation and creativity. And also I focus more on the solution you need rather than the system itself.</p>
                    <p>Maybe this webpage is a little "empty".. probably I will fill it later</p>
                    <p>But, hey. If you want to know more about me.. go on and let me know</p>
                    <p>My
                      <b>Twitter</b> is
                      <a href="https://twitter.com/guillermocroppi" class="has-text-info">@guillermocroppi</a>
                    </p>
                    <p>The
                      <b>Email</b> is my name and surname, all together, "@gmail.com"</p>
                    <p>I will wait for your contact!
                      <br>Cheers!</p>
                  </div>
                  <div v-show="language == 'spanish'" class="content has-text-centered">
                    <p>Soy Ingeniero en Sistemas de Información, y soy fullstack web developer</p>
                    <p>Trabajo como freelancer (independiente), ya que me me gusta conocer a mis clientes y trabajar con ellos</p>
                    <p>No hago sistemas.
                      <b>Yo hago soluciones</b>. Soluciones que responden a tus necesidades y resuelven tus problemas. Si, muy probablemente habra un sistema, pero, ¿Que hoy no cuenta con un sistema, no?</p>
                    <p>¿Porque elegirme? Porque cuido mucho de mis sistemas, y entrego presentación y creatividad. Y también me enfoco mas en la solución que estas necesitando antes que otras cosas (que no quita que sea relevante).</p>
                    <p>Tal vez esta página pueda verse un poco "vacia".. probablemente la llenaré mas tarde</p>
                    <p>Pero hey. Si quiere saber más acerca de mí puede encontrarme en Twitter o escribirme a mi email</p>
                    <p>Mi
                      <b>Twitter</b> es
                      <a href="https://twitter.com/guillermocroppi" class="has-text-info">@guillermocroppi</a>
                    </p>
                    <p>El
                      <b>Email</b> es mi nombre y apellido, todo junto, "@gmail.com"</p>
                    <p>¡Estaré a la espera por su contacto!
                      <br>¡Saludos!</p>
                  </div>
                </div>
                <hr>
                <div id="projects" v-show="language == 'english'" class="with-padding">
                  <h1 class="title is-3 is-oswald is-400">Projects</h1>
                  <h1 class="subtitle is-4 is-oswald is-300">Coming soon...</h1>
                </div>
                <div id="projects" v-show="language == 'spanish'" class="with-padding">
                  <h1 class="title is-3 is-oswald is-400">Proyectos</h1>
                  <h1 class="subtitle is-4 is-oswald is-300">Proximamente...</h1>
                </div>
              </div>
            </div>
          </section>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'hello',
  data() {
    return {
      language: 'english'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
img {
  margin: 0px auto;
}

.content p {
  font-size: 0.85rem
}

.column {
  padding-left: 0;
  padding-right: 0;
  padding-bottom: 0;
}

span {
  font-size: 20px;
}

.hero.is-halfheight .hero-body,
.hero.is-fullheight .hero-body{
  align-items: baseline;
  padding: 0;
  .with-padding {
    padding: 2rem 1.5rem;
  }
}
</style>



